@import url(https://fonts.googleapis.com/css?family=Lilita+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#title {
	font-family: 'Cinzel', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/**
 * App header styles
 * 
 */
.App-header {
  background-color: #282c34;
/*  min-height: 100vh;*/
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
	height:215px;
}
.App-header * {
	display: inline;
  
}
.App-nav {
/*	font-family: 'Cinzel', serif;*/
	font-family: 'Lilita One', cursive;
	font-weight: 1;
	color: white;
	display: inline;
	font-size: 27px;
}
.nav-item {
color: white;
	text-decoration: none;
}

li {
	padding: 10px;
}

#title {
  font-family: 'Lilita One', cursive;
  text-shadow: 1px 1px 1px white!important; 
	font-size: 60px;
	font-weight: 100;
	color: #a848c2;
  margin-bottom: 30px;
/*	color: white;*/
}
html, body {
  height: 100%;
}

body {
    background-color: #282c34;
}

#body {
  min-height: 100%;
  height: 100%;
  padding: 20px;
  padding-left: 40px;
  padding-right: 40px;
/*  color: #c4b4bc;*/
  color: white;
  font-size: 13px;
}
.row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 60%;
  text-align: center;
}

.column {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.sidebar {
  border-right: 1px dotted #8f8c8a;
  height: 100%;
  padding: 20px;
  padding-top: 5px;
}
.content {
  padding: 30px;
}

#footer {
  position: absolute;
  background-color: #282c34;
  color: white;
  text-align: center;
  margin: 0 auto;
  bottom: 0;
  width: 100%;
  height: 2rem;/* Footer height */
  font-size: 12px;
}

a:active {
	color: #df41ff;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#purple {
  color: #e1aaff;
}

